.form {
  margin: 90px 70px;
  width: 300px;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.form label {
  width: 100%;
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

.form input {
  width: 100%;
  padding: 8px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  background: #eff7ef;
  border-radius: 4px;
  box-sizing: border-box;
  box-shadow: 0 0 0 30px #eff7ef inset;
}
.form input:focus,
.form input:hover {
  border-color: #347a3f;
  outline: none;
}

.form button {
  width: 100%;
  padding: 10px;
  background-color: #057018;
  color: white;
  font-size: 14px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.form button:hover {
  background-color: #1a5e26;
}

.registerLink {
  margin-top: 15px;
  text-align: center;
}

.register {
  color: #057018;
  text-decoration: none;
}

.register:hover {
  text-decoration: underline;
}

.error {
  color: #d8000c;
  padding: 10px;
  margin-top: 10px;
  text-align: center;
}


